// asset:<package-name>/<realm>/<path-to-module>
const /** @type {?} */ _ASSET_URL_RE = /asset:([^\/]+)\/([^\/]+)\/(.+)/;

/**
 * Interface that defines how import statements should be generated.
 */
export abstract class ImportGenerator {
/**
 * @param {?} url
 * @return {?}
 */
static parseAssetUrl(url: string): AssetUrl { return AssetUrl.parse(url); }
/**
 * @abstract
 * @param {?} moduleUrlStr
 * @param {?} importedUrlStr
 * @return {?}
 */
getImportPath(moduleUrlStr: string, importedUrlStr: string) {}
}

export class AssetUrl {
/**
 * @param {?} url
 * @param {?=} allowNonMatching
 * @return {?}
 */
static parse(url: string, allowNonMatching: boolean = true): AssetUrl {
    const /** @type {?} */ match = url.match(_ASSET_URL_RE);
    if (match !== null) {
      return new AssetUrl(match[1], match[2], match[3]);
    }
    if (allowNonMatching) {
      return null;
    }
    throw new Error(`Url ${url} is not a valid asset: url`);
  }
/**
 * @param {?} packageName
 * @param {?} firstLevelDir
 * @param {?} modulePath
 */
constructor(public packageName: string,
public firstLevelDir: string,
public modulePath: string) {
  }

  static _tsickle_typeAnnotationsHelper() {
 /** @type {?} */
AssetUrl.prototype.packageName;
 /** @type {?} */
AssetUrl.prototype.firstLevelDir;
 /** @type {?} */
AssetUrl.prototype.modulePath;
  }

}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
